#!/usr/bin/env node

import { Command } from 'commander';
import chalk from 'chalk';
import inquirer from "inquirer";
import { templates } from './templates';
import ora from 'ora';
import Actions from './actions';
import { join } from 'node:path';

const program = new Command();

program
    .name('create-h3ravel')
    .description('CLI to create new h3ravel app')
    .version('0.1.0');

program
    .option("-n, --name <type>", "Add your name")
    .action(async (options) => {
        console.log(options)

        const { appName, template, install } = await inquirer
            .prompt([
                {
                    type: "input",
                    name: "appName",
                    message: "What is the name of your project?",
                    default: 'h3ravel'
                },
                {
                    type: "list",
                    name: "template",
                    message: "Select starter template",
                    choices: <never>templates.map(e => ({
                        name: e.name,
                        value: e.alias,
                        disabled: !e.source ? '(Unavailable at this time)' : false,
                    })),
                },
                {
                    type: 'confirm',
                    name: "install",
                    message: "Would you want to install node_modules right away?",
                    default: true,
                },
            ])

        const actions = new Actions(join(process.cwd(), String(appName).toLowerCase().replaceAll(' ', '-')));

        const spinner = ora(`Loading Template...`).start();
        await actions.download(templates.find(e => e.alias === template)?.source!, install);

        spinner.info(chalk.green("Cleaning Up...")).start();
        await actions.cleanup()

        spinner.info(chalk.green("Creating .env...")).start();
        await actions.copyExampleEnv()

        spinner.succeed(chalk.green('Template Downloaded!'))

        await actions.complete()
    });

program.parse();
